body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #121213;
  color: #d7dadc;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 13px;
}

.row {
  display: flex;
  flex-direction: row;
}

.cell {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 62px;
  height: 62px;
  font-size: 24px;
  border: 2px solid #121213;
  border-radius: 5px;
  text-transform: uppercase;
}

.bottomFixed {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 43px;
  height: 58px;
  font-size: 16px;
  border: 2px solid #121213;
  border-radius: 8px;
  text-transform: uppercase;
  cursor: pointer;
  user-select: none;
  background: #676769;
}

.button.guessed {
  background: #538d4e;
}
.button.usedInWord {
  background: #b59f3b;
}
.button.used {
  background: #3a3a3c;
}

.button:hover {
  background-color: #8f8f8f;
}

@media only screen and (max-width: 600px) {
  .cell {
    width: 50px;
    height: 50px;
  }

  .button {
    width: 27px;
    height: 38px;
  }
}
